/* ========================================================================
 * DOM-based Routing
 * Based on http://goo.gl/EUTi53 by Paul Irish
 *
 * Only fires on body classes that match. If a body class contains a dash,
 * replace the dash with an underscore when adding it to the object below.
 *
 * .noConflict()
 * The routing is enclosed within an anonymous function so that you can
 * always reference jQuery with $, even when in .noConflict() mode.
 * ======================================================================== */

(function($) {

  // Use this variable to set up the common and page specific functions. If you
  // rename this variable, you will also need to rename the namespace below.
  var Sage = {
    // All pages
    'common': {
      init: function() {
        // JavaScript to be fired on all pages
        $('.hamburger').click(function() {
          $('body').toggleClass('menu-active');
        });



        $('ul.nav > li').click(function(e) {
          if( $(e.target).is(this)) {
            e.preventDefault();
            $(this).toggleClass('active');
          }
        });

        // Swiper
        $(document).ready(function () {
          //initialize swiper when document ready

          var mySwiper;

          var swiperfunction = function() {
            if(!mySwiper) {
                mySwiper = new Swiper ('.nice-to-meet .swiper-container', {
                  pagination: '.nice-to-meet .swiper-pagination',
                  slidesPerView: 'auto',
                  centeredSlides: true,
                  paginationClickable: true,
                  spaceBetween: 0,
                  loop: false
                });
              }
          };

          if($(window).width() < 750 ) {
            swiperfunction();
          }

          $(window).on('resize', function() {

            if($(window).width() > 750 ) {
              console.log(mySwiper);
              if(mySwiper) {
                mySwiper.destroy(true, true);
                mySwiper = undefined;
              }
            } else {
              swiperfunction();
            }

          });


          if( $('.business-management').length > 0 ) {
            var business_swiper = new Swiper ('.business-management .swiper-container', {
              pagination: '.business-management .swiper-pagination ul',
              paginationClickable: true,
              paginationBulletRender: function (index, className) {
                var img = '<div class="img-frame">' + $('.business-management .swiper-slide:nth-child(' + (index + 1) + ') .img-frame').html() + '</div>';
                var name = '<h4>' + $('.business-management .swiper-slide:nth-child(' + (index + 1) + ') h4').html() + '</h4>';
                var title = '<p class="work-title">' + $('.business-management .swiper-slide:nth-child(' + (index + 1) + ') .work-title').html() + '</p>';
                return '<li class="' + className + '">' + img + '<div class="pagination-text">' + name + title + '</div></li>';
              },
              prevButton: '.prev-slide',
              nextButton: '.next-slide',
              centeredSlides: true,
              spaceBetween: 0,
              loop: false
            });
          }

          $('.tag-select').on('change', function (ev) {
            var val = $(this).val();
            if (val === '' || val.length <= 1) {
              return $('.media-image').show();
            }
            $('.media-image:not([class*="'+val+'"])').hide();
            $('.media-image[class*="'+val+'"]').show();
          });

        });

        // Fade in elements when in viewport
        new WOW().init({
          boxClass:     'wow',
          animateClass: 'animated',
          offset:       2000,
          mobile:       true,
          live:         false
        });


        $.stellar({ horizontalScrolling: false, verticalOffset: 0, horizontalOffset: 0 }); // parallax


        // Google map function
        var googleMap = function() {
          var mapEl = $('#google-map')[0];
          var geocoder = new google.maps.Geocoder();
          var address = $('#google-map').attr('data-address');

          geocoder.geocode( { 'address': 'Rosenlundsgatan 29'}, function(results, status) {
            if (status === google.maps.GeocoderStatus.OK) {

              var mapOptions = {
                  zoom: 14,
                  scrollwheel: false,
                  center: results[0].geometry.location,
                  disableDefaultUI: true
              };
              var map = new google.maps.Map(mapEl, mapOptions);
              var marker = new google.maps.Marker({
                map: map,
                position: results[0].geometry.location,
                icon: 'app/themes/leovegas/assets/images/google-map-marker.png'
              });

            } else {
              alert("Geocode was not successful for the following reason: " + status);
            }
          });
        };

        if ( $('#google-map').length > 0 ) {
          googleMap();
        }

        $('.dropdown').click(function() {
            $(this).toggleClass('active');
        });

        var sortReportList = function(year, list_item) {
          console.log(year);
          if( year === 'start') {
            $(list_item).removeClass('hidden');
          } else {
            $(list_item).addClass('hidden').removeClass('no-border');
            $(list_item + '.year-' + year).removeClass('hidden');
            $(list_item + ':not(.hidden)').last().addClass('no-border');
          }
        };

        // Add sorting to Interim report lisat - IR section
        $('.interim-report-select').on('change', function() {
            sortReportList( $(this).val(), '.interim-report-list li' );
        });

        /// Add sorting to Press release list - IR section
        $('.press-release-select').on('change', function() {
          sortReportList( $(this).val(), '.press-release-list li' );
        });

        // Add sorting to Annual report list - IR section
        $('.annual-report-select').on('change', function() {
            sortReportList( $(this).val(), '.annual-report-list li' );
        });

        // Sort function for Quarterly presentation - IR section
        $('.quarterly-reports-select').on('change', function() {
          if( $(this).val() !== 'start') {
            $('.quarterly-reports').removeClass('visible');
            $('.quarterly-reports.' + 'year-' + $(this).val()).addClass('visible');
          }
        });

      },
      finalize: function() {
        // JavaScript to be fired on all pages, after page specific JS is fired
      }
    },
    // Home page
    'home': {
      init: function() {
        // JavaScript to be fired on the home page
      },
      finalize: function() {
        // JavaScript to be fired on the home page, after the init JS
      }
    },
    // About us page, note the change from about-us to about_us.
    'about_us': {
      init: function() {
        // JavaScript to be fired on the about us page
      }
    }
  };

  // The routing fires all common scripts, followed by the page specific scripts.
  // Add additional events for more control over timing e.g. a finalize event
  var UTIL = {
    fire: function(func, funcname, args) {
      var fire;
      var namespace = Sage;
      funcname = (funcname === undefined) ? 'init' : funcname;
      fire = func !== '';
      fire = fire && namespace[func];
      fire = fire && typeof namespace[func][funcname] === 'function';

      if (fire) {
        namespace[func][funcname](args);
      }
    },
    loadEvents: function() {
      // Fire common init JS
      UTIL.fire('common');

      // Fire page-specific init JS, and then finalize JS
      $.each(document.body.className.replace(/-/g, '_').split(/\s+/), function(i, classnm) {
        UTIL.fire(classnm);
        UTIL.fire(classnm, 'finalize');
      });

      // Fire common finalize JS
      UTIL.fire('common', 'finalize');
    }
  };

  // Load Events
  $(document).ready(UTIL.loadEvents);

})(jQuery); // Fully reference jQuery after this point.
